body{
  margin: 0;
  padding: 0;
  background: #EFEEEE;
}
.card{
  display:flex;
  align-items: center;
  margin: 10px 70px;
  width: 300px;
  gap:20px;
  border-radius: 20px;
  background-color: #EFEEEE ;
  box-shadow: 3px 3px 7px #FFFFFF,-3px -3px 7px #D1CDC7;
  transition:1s;
}

.card:hover{
  transform: scale(1.08);
}
img{
  width: 6rem;
  padding: 5px;
}